@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.0/animate.min.css);
@import url(https://fonts.googleapis.com/css?family=Roboto);
* {
  box-sizing: border-box;
  font-family: 'Roboto', Arial, Helvetica, sans-serif; }

body {
  background-color: #ffffff;
  color: #3d3d3d;
  margin: 0; }

button {
  margin: auto 10px;
  background-color: #f1f1f1;
  color: #222;
  border: none;
  padding: 0px;
  text-align: center; }

button:active {
  -webkit-transform: scale(0.85);
          transform: scale(0.85); }

button:hover {
  cursor: pointer;
  background-color: #f7f7f7; }

.noselect {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default; }

.container {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
  padding: 0.8em; }

.appname {
  font-size: 56px;
  text-align: center;
  padding-bottom: 24px;
  color: #9dfaeec2; }

.todo-input {
  width: 100%;
  padding: 10px 18px;
  font-size: 18px;
  margin-bottom: 16px;
  border: none;
  outline: none;
  background-color: #eeeeee;
  border-radius: 5px; }

.new-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start; }
  .new-item input {
    margin: auto; }
  .new-item button {
    margin: auto 10px;
    width: 35px;
    height: 32px;
    border-radius: 50%;
    border: none;
    padding: 0px;
    font-size: 16px;
    text-align: center; }
  .new-item button:after {
    display: block;
    content: '\2715';
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }

.settings-con {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  margin-top: 10px;
  margin-bottom: 5px; }
  .settings-con .settings-btn {
    margin-left: 0;
    display: block;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    height: 28px;
    padding: 0 10px;
    border-radius: 7px;
    font-size: 12px;
    text-transform: uppercase;
    text-align: center; }
  .settings-con .downloadbtn:after {
    display: block;
    font-size: 14px;
    content: '\21E9'; }
  .settings-con .uploadbtn:after {
    display: block;
    font-size: 14px;
    content: '\21E9';
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg); }
  .settings-con .settings-btn:hover {
    cursor: pointer; }
  .settings-con .settings-btn:active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9); }
  .settings-con .rightside {
    justify-self: flex-end;
    text-align: right; }

.todo-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: stretch;
          justify-content: stretch;
  text-align: left; }
  .todo-item p {
    font-size: 18px;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    width: 300px;
    word-wrap: break-word;
    margin: 12px auto; }
  .todo-item .remove-item {
    padding: 0;
    border: none;
    height: 50%;
    margin: auto 10px;
    padding: 0 5px;
    background: none;
    font-size: 14px; }
  .todo-item .remove-item:hover {
    cursor: pointer; }
  .todo-item .remove-item:active {
    color: #3d3d3d;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    background: none; }

.completedTask {
  text-decoration: line-through;
  color: gray; }

.hideItem {
  display: none; }

.form-checkbox {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  display: inline-block;
  position: relative;
  background-color: #f1f1f1;
  color: #666;
  height: 30px;
  width: 30px;
  border-radius: 50px;
  cursor: pointer;
  margin: auto 10px;
  outline: none;
  border: none;
  transition: all 0.2s; }

.form-checkbox:checked::before {
  position: absolute;
  font: 13px/1 'Open Sans', sans-serif;
  left: 11px;
  top: 7px;
  content: '\2143';
  -webkit-transform: rotate(40deg);
          transform: rotate(40deg); }

.form-checkbox:hover {
  background-color: #f7f7f7; }

.form-checkbox:checked {
  background-color: #f1f1f1; }

@media only screen and (min-width: 768px) {
  .form-checkbox {
    margin-left: 0px; }
  .remove-item:hover {
    -webkit-transform: scale(1.4);
            transform: scale(1.4); } }

/* Text and background color for dark mode */
@media (prefers-color-scheme: dark) {
  body {
    color: lightgray;
    background-color: #222; }
  .appname {
    color: #329e8cc2; }
  .remove-item {
    color: #a1a1a1; }
  .todo-input {
    background-color: #3d3d3d;
    color: lightgray; }
  .new-item button {
    color: #c7c7c7;
    background-color: #666; }
  .new-item button:hover {
    color: #c7c7c7;
    background-color: #919191; }
  button {
    color: #c7c7c7;
    background-color: #666; }
  button:hover {
    color: #c7c7c7;
    background-color: #919191; }
  .form-checkbox {
    color: #c7c7c7;
    background-color: #666; }
  .form-checkbox:hover {
    background-color: #919191; }
  .form-checkbox:checked {
    background-color: #666; } }

